@import url(https://fonts.googleapis.com/css2?family=Inter&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


.app__joke{
    width: 439px;
  
    width:100%;
    height: 44px;
    margin: 24px 0 54px;
    font-family: 'Inter', sans-serif;
    font-size: 18px;
    font-weight: 600;
    font-stretch: normal;
    font-style: italic;
    line-height: normal;
    letter-spacing: normal;
    text-align: left;
    color: #34394f;
  }
.categories-container{
    height: 58px;
    width: 439px;

    width:100%;
    margin: 32px auto 16px;
}
.categorie{
    position:relative;  
}

.categories__select{
  position: absolute;
  width: 439px;

  width:264px;
  border: solid 3px #c4c4c4;
  border-radius: 6px;
  padding:3px;
  background-color: #fff;
}
@media (min-width: 360px) {
  .categories__select{
     width:304px;
  }
}
@media (min-width: 600px) {
  .categories__select{
     width:439px;
  }
}
.categories__category i{
font-size: 12px;
}

.categories__category{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items:center;
          align-items:center;
  height: 58px;
  padding: 16px;
  border-radius: 6px;
  cursor: pointer;

  font-family: 'Inter', sans-serif;
  font-size: 16px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.63;
  letter-spacing: -0.52px;
  text-align: left;
  color: #c4c4c4;
}

.categories__option{
    display:none;
}
.categories__option--active{
    display:block;
}

.categories__category--dark{
   color: #34394f;
   border-color: #34394f;
}

.categories__category--dark:hover{
  background-color: #e9e9e9;
  font-weight: 600;
}
body{
	 background-color: #f9f9f9;
}

.app{
    position:absolute;
    top:0;
    left:50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;

    width:320px;
    height: 654px;
    padding: 28px 28px 72px;   
    border-radius: 20px;
    box-shadow: 0 4px 12px 0 rgba(0, 0, 0, 0.08);
    background-color: white;
}
@media (min-width: 360px) {
  .app{
     width:360px;
  }
}

@media (min-width: 600px) {
  .app{
     width: 555px;
     padding: 48px 58px 72px;
     height: 704px;
  }
}

@media (min-height: 654px) {
  .app{
     top: 50%;
     -webkit-transform: translate(-50% , -50%);
             transform: translate(-50% , -50%);
  };
}

.app__img{
    width:100%;
    margin: 24px 0 24px;
    border-radius:5px;  
}
@media (min-width: 600px) {
  .app__img{
     width: 439px;
    height: 130px;
  }
}

.app__impersonate{
  width: 439px;

  width:100%;
  height: 58px;
  margin: 16px 0 32px;
  padding: 16px;
  border: solid 3px #c4c4c4;
  border-radius: 6px;
  background-color: white;

  font-family: 'Inter', sans-serif;
  font-size: 16px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.63;
  letter-spacing: -0.52px;
  text-align: left;
  color: #34394f;
}

::-webkit-input-placeholder { 
  color: #c4c4c4;
  
}

:-ms-input-placeholder { 
  color: #c4c4c4;
  
}

::placeholder { 
  color: #c4c4c4;
  
}


.app__submit{
  width: 439px;

  width:100%;
  height: 58px;
  margin: 32px 0 0;
  padding: 16.8px 10 15.2px ;
  border-radius: 6px;
  background-color: #34394f;

  font-family: Inter;
  font-size: 16px;
  font-weight: 600;
  font-stretch: normal;
  font-style: normal;
  text-align: center;
  color: white;
}
